@font-face {
	font-family: 'bebas';
	font-size: normal;
	font-weight: 400;
	src: url('../fonts/bebas.woff');
	src: url('../fonts/bebas.woff2'); }

@include font('bebas-font',bebas);
body {
	font-family: Helvetica, sans-serif;
	background: #000;
	color: #FFF; }
@mixin bebas {
	font-family: bebas, sans-serif; }


.container {
	max-width: 940px;
	margin: 0 auto;
	@include r(840) {
		margin: 0 10px; } }
.button {
	display: inline-block;
	font-size: 12px;
	line-height: 31px;
	padding: 0 13px;
	font-weight: bold;
	color: #fff;
	text-decoration: none;
	background: #000;
	transition: .5s ease-out;

	@include bebas;
	@include r(640);

	&:hover {
		border-radius: 25px;
		background: rgba(#E02727,.45);
		letter-spacing: 5px; } }




.h3 {
	font-size: 18px;
	color: #E02727;
	text-transform: uppercase;
	display: flex;
	line-height: 1;
	margin-bottom: 43px;
	word-spacing: 5px;
	letter-spacing: 1px;
	@include bebas;
	&:after {
		flex: 1;
		content: "";
		background: url(../img/pattern.png) repeat;
		left: 100%;
		margin-left: 21px; } }
