.header {
	margin: 21px 0;
	@include bebas;
	@include r(650) {
		display: block;
		text-align: center;
		margin-top: 0; }
	&__wrap {
		display: flex;
		align-items: baseline;
		@include r(650) {
			display: block; } }
	&__logo {
		font-size: 0;
		text-decoration: none;
		@include r(650) {
			display: block;
			margin-bottom: 20px;
			padding-top: 25px; } }

	&__nav {
		margin-left: auto;
		@include r(650) {
			line-height: 3; } } }

.nav {
	position: relative;
	top: -6px;
	font-size: 22px;
	a {
		text-decoration: none;
		color: #E02727;
		margin-left: 27px;
		transition: .25s ease-in;
		&:active,&:hover {
			color: #fff; } } }


.logo__part1,.logo__part2 {
	font-size: 36px;
	text-decoration: none;
	color: #E02727;
	display: inline-block;
	transition: .5s ease-out;
	&:hover {
		color: #fff; } }
.logo__part2 {
	color: #f3af9d;
	&:hover {
		color: #fff; } }


.hero {
	overflow: hidden;
	&__wrap {
		position: relative;
		&:before,&:after {
			content: "";
			position: absolute;
			top: 0;
			background: #E02727;
			height: 60px;
			width: 100%; }
		&:before {
			right: 100%;
			margin-right: 10px; }
		&:after {
			left: 100%;
			margin-left: 10px; } } }



.slider {
	position: relative;

	&__slides {}

	&__slide {
		height: 0;
		padding-bottom: 42%;
		@include r(620) {
			padding-bottom: 70%; } }



	&__next,&__prev {
		left: 20px;
		position: absolute;
		font-size: 54px;
		top: 40%;
		z-index: 100; }


	&__next {
		transform: rotate(-180deg);
		margin-top: -8px;
		right: 20px; } }
.slider {

	.icon {
		cursor: pointer;
		fill: rgba(#fff, .2); } }

.slide {
	position: relative;
	&__content {
		position: absolute;
		bottom: 31px;
		left: 36px;
		width: 413px;
		padding: 17px;
		background: rgba(#000,.45);
		color: rgba(#fff, .85);
		@include r(620) {
			background: none;
			top: 10px;
			left: 60px;
			width: 70%; }

		@include r(840) {
			left: 100px; } }

	&__title {
		text-transform: uppercase;
		font-size: 18px;
		margin-bottom: 17px;
		word-spacing: 5px;
		letter-spacing: 2px;
		@include bebas; }
	&__text {
		font-size: 12px;
		line-height: 1.4; } }


.thumbs {
	margin-top: 13px;
	margin-bottom: 61px;
	ul {
		display: grid;
		grid-template-columns: repeat(6,1fr);
		grid-gap: 17px;
		@include r(620) {
			grid-template-columns: repeat(3,1fr); }

		li {
			a {
				display: block;
				padding-bottom: 60%;
				border-radius: 3px;
				border: 2px solid transparent;
				&:hover,&:active {
					transition: .35s ease-out;
					box-shadow: 0 0 3px #fff;
					border: 2px solid #000;
					border-radius: 3px; }
				&:active {
					box-shadow: 0 0 10px #fff; } } } } }



.features {
	&__wrap {
		display: grid;
		grid-template-columns: repeat(3,1fr);
		grid-gap: 47px;
		margin-bottom: 65px;
		letter-spacing: 1px;
		// color: rgba(#FFF, .8)


		@include r(840) {
			display: block;
			margin-bottom: 70px; } } }


.feature {
	.button {
		float: right; }
	@include r(840) {
		margin-bottom: 70px;
		&:last-child {
			margin-bottom: 100px; }
		.button {
			float: right; } }

	&__title {
		display: flex;
		margin-bottom: 31px;

		@include r(840) {
			margin-bottom: 10px; }

		h2 {
			height: 0;
			// color: #E02727
			color: rgba(#FFF, .8);
			font-size: 18px;
			line-height: 1.3;
			text-transform: uppercase;
			position: relative;
			@include bebas;
			@include r(840) {
				line-height: 40px; }
			@include r(620) {
				line-height: 1.3; } } }

	&__icon {
		.icon {
			flex: 0;
			display: inline;
			font-size: 46px;
			margin-right: 21px;
			line-height: 0;
			fill: rgba(#fff, .7); } }
	&__text {
		font-size: 12px;
		margin-bottom: 17px;
		@include r(840) {
			margin-bottom: 15px;
			line-height: 1.6;
			font-size: 14px; } } }


.works {
	margin-bottom: 46px; }
.work {
	display: block;
	text-decoration: none;
	word-spacing: 5px;
	letter-spacing: 1px;

	&:hover,&:active {
		.work__title {
			box-sizing: border-box;
			color: #fff;
			transition: .5s ease-out;
			letter-spacing: 3px;
			@include r(640) {
				letter-spacing: 2px; } }

		.work__date {
			transition: .5s ease-out;
			letter-spacing: 2px; } }

	&__wrap {
		display: grid;
		grid-template-columns: repeat(4,1fr);
		grid-gap: 17px;
		@include r(620) {
			grid-template-columns: repeat(2,1fr); } }
	&__image {
		overflow: hidden;
		position: relative;
		display: block;
		padding-bottom: 70%;
		img {
			display: block;
			max-width: 100%;
			position: absolute;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0; } }

	&__bottom {
		border: 1px solid #dbdbdb;
		border-top: none;
		padding: 13px;
		text-transform: uppercase; }
	&__title {
		font-size: 16px;
		margin-bottom: 5px;
		color: #b9b9b9;
		@include bebas; }

	&__date {
		font-size: 11px;
		line-height: 1;
		color: #E02727;
		@include bebas; } }


.footer, .footer__whitelinks {
	color: rgba(#fff, .59);
	overflow: hidden;
	&:hover {
		transition: 1s;
		color: #FFF;
		.footer__whitelinks {
			transition: 1s;
			color: #FFF; } }

	&__wrap {
		padding: 36px 36px 30px 36px;
		background: rgba(#E02727,.8);
		max-width: 940px;
		position: relative;
		display: grid;
		grid-template-columns: repeat(3,1fr);
		grid-gap: 17px;
		&:before,&:after {
			content: "";
			position: absolute;
			width: 100%;
			top: 0;
			height: 100%;
			background: rgba(#E02727,.3); }
		&:before {
			margin-right: 10px;
			right: 100%; }

		&:after {
			margin-left: 10px;
			left: 100%; }
		a {
			text-decoration: none; } }
	&__col {
		@include r(620) {
			margin-bottom: 30px;
			&:last-of-type {
				margin-bottom: 0; } } }
	&__title {
		text-transform: uppercase;
		font-size: 20px;
		margin-bottom: 24px;
		@include bebas;
		@include r(620) {
			margin-bottom: 10px; } }
	&__text {
		font-size: 12px;

		p {
			margin-bottom: 15px;
			@include r(620) {
				&:nth-child(odd) {
					display: none; } } } } }

.tweet {
	margin-bottom: 15px;
	font-size: 12px;
	@include r(620) {
		&:nth-child(odd) {
			display: none; } }

	a {
		color: #f3b29d; }
	&__text {
		font-size: 12px;
		margin-bottom: 5px; } }


.wrapper {
	background: #000;
	position: relative;
	z-index: 200;
	box-shadow: 0 0 50px rgba(#000, .8); }
body {
	padding-bottom: 45vh;
	@include r(820) {
		padding-bottom: 70vh; }
	@include r(640) {
		padding-bottom: 100vh; } }


.footer {
	height: 80vh;
	position: fixed;
	bottom: 0;
	z-index: 100;
	left: 15px;
	right: 15px;
	@include r(640) {
		height: 120vh; }


	&__wrap {
		position: absolute;
		bottom: 0;
		@include r(640) {
			display: block;
			padding-bottom: 0; } } }






.portfolio {
	overflow: hidden;
	&__wrap {
		position: relative;
		background: #E02727;
		margin-bottom: 30px;

		&:before,&:after {
			content: "";
			position: absolute;
			top: 0;
			background: #f3af9d;
			height: 100%;
			width: 100%; }
		&:before {
			right: 100%;
			margin-right: 10px; }
		&:after {
			left: 100%;
			margin-left: 10px; } }
	&__title {
		display: inline-block;
		color: #FFF;
		text-transform: uppercase;
		@include bebas;
		padding: 0 30px;
		font-size: 24px;
		line-height: 48px; } }

.portfolio-item__image {
	margin-bottom: 30px; }

.portfolio-item__text {
	margin-bottom: 30px;
	p {
		margin-bottom: 15px; } }

.content {
	.container {
		overflow: hidden; } }

.content {
	.container {
		img {
			background-size: cover; } } }
