html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-arrow {
  width: 0.69em;
  height: 1em;
  fill: #FFF;
}

.icon-check {
  width: 1.2em;
  height: 1em;
  fill: #E8663C;
}

.icon-cog {
  width: 1em;
  height: 1em;
  fill: #E8663C;
}

.icon-user {
  width: 1.05em;
  height: 1em;
  fill: #E8663C;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

@media only screen and (max-width: 620px) {
  .container {
    margin: 0 10px;
  }
}

@media only screen and (max-width: 620px) {
  .header {
    display: block;
    text-align: center;
  }
}

@media only screen and (max-width: 620px) {
  .header__wrap {
    display: block;
  }
}

@media only screen and (max-width: 620px) {
  .header__logo {
    margin-bottom: 30px;
  }
}

.header__nav {
  line-height: 2;
}

@media only screen and (max-width: 620px) {
  .slider__next {
    left: 10px;
  }
}

@media only screen and (max-width: 620px) {
  .slider__prev {
    right: 10px;
  }
}

@font-face {
  font-family: 'bebas';
  font-size: normal;
  font-weight: 400;
  src: url("../fonts/bebas.woff");
  src: url("../fonts/bebas.woff2");
}

@font-face {
  font-family: "bebas-font";
  src: url("../fonts/bebas.woff2") format("woff2"), url("../fonts/bebas.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

body {
  font-family: Helvetica, sans-serif;
  background: #000;
  color: #FFF;
}

.container {
  max-width: 940px;
  margin: 0 auto;
}

@media only screen and (max-width: 840px) {
  .container {
    margin: 0 10px;
  }
}

.button {
  display: inline-block;
  font-size: 12px;
  line-height: 31px;
  padding: 0 13px;
  font-weight: bold;
  color: #fff;
  text-decoration: none;
  background: #000;
  transition: .5s ease-out;
  font-family: bebas, sans-serif;
}

.button:hover {
  border-radius: 25px;
  background: rgba(224, 39, 39, 0.45);
  letter-spacing: 5px;
}

.h3 {
  font-size: 18px;
  color: #E02727;
  text-transform: uppercase;
  display: flex;
  line-height: 1;
  margin-bottom: 43px;
  word-spacing: 5px;
  letter-spacing: 1px;
  font-family: bebas, sans-serif;
}

.h3:after {
  flex: 1;
  content: "";
  background: url(../img/pattern.png) repeat;
  left: 100%;
  margin-left: 21px;
}

.header {
  margin: 21px 0;
  font-family: bebas, sans-serif;
}

@media only screen and (max-width: 650px) {
  .header {
    display: block;
    text-align: center;
    margin-top: 0;
  }
}

.header__wrap {
  display: flex;
  align-items: baseline;
}

@media only screen and (max-width: 650px) {
  .header__wrap {
    display: block;
  }
}

.header__logo {
  font-size: 0;
  text-decoration: none;
}

@media only screen and (max-width: 650px) {
  .header__logo {
    display: block;
    margin-bottom: 20px;
    padding-top: 25px;
  }
}

.header__nav {
  margin-left: auto;
}

@media only screen and (max-width: 650px) {
  .header__nav {
    line-height: 3;
  }
}

.nav {
  position: relative;
  top: -6px;
  font-size: 22px;
}

.nav a {
  text-decoration: none;
  color: #E02727;
  margin-left: 27px;
  transition: .25s ease-in;
}

.nav a:active, .nav a:hover {
  color: #fff;
}

.logo__part1, .logo__part2 {
  font-size: 36px;
  text-decoration: none;
  color: #E02727;
  display: inline-block;
  transition: .5s ease-out;
}

.logo__part1:hover, .logo__part2:hover {
  color: #fff;
}

.logo__part2 {
  color: #f3af9d;
}

.logo__part2:hover {
  color: #fff;
}

.hero {
  overflow: hidden;
}

.hero__wrap {
  position: relative;
}

.hero__wrap:before, .hero__wrap:after {
  content: "";
  position: absolute;
  top: 0;
  background: #E02727;
  height: 60px;
  width: 100%;
}

.hero__wrap:before {
  right: 100%;
  margin-right: 10px;
}

.hero__wrap:after {
  left: 100%;
  margin-left: 10px;
}

.slider {
  position: relative;
}

.slider__slide {
  height: 0;
  padding-bottom: 42%;
}

@media only screen and (max-width: 620px) {
  .slider__slide {
    padding-bottom: 70%;
  }
}

.slider__next, .slider__prev {
  left: 20px;
  position: absolute;
  font-size: 54px;
  top: 40%;
  z-index: 100;
}

.slider__next {
  transform: rotate(-180deg);
  margin-top: -8px;
  right: 20px;
}

.slider .icon {
  cursor: pointer;
  fill: rgba(255, 255, 255, 0.2);
}

.slide {
  position: relative;
}

.slide__content {
  position: absolute;
  bottom: 31px;
  left: 36px;
  width: 413px;
  padding: 17px;
  background: rgba(0, 0, 0, 0.45);
  color: rgba(255, 255, 255, 0.85);
}

@media only screen and (max-width: 620px) {
  .slide__content {
    background: none;
    top: 10px;
    left: 60px;
    width: 70%;
  }
}

@media only screen and (max-width: 840px) {
  .slide__content {
    left: 100px;
  }
}

.slide__title {
  text-transform: uppercase;
  font-size: 18px;
  margin-bottom: 17px;
  word-spacing: 5px;
  letter-spacing: 2px;
  font-family: bebas, sans-serif;
}

.slide__text {
  font-size: 12px;
  line-height: 1.4;
}

.thumbs {
  margin-top: 13px;
  margin-bottom: 61px;
}

.thumbs ul {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-gap: 17px;
}

@media only screen and (max-width: 620px) {
  .thumbs ul {
    grid-template-columns: repeat(3, 1fr);
  }
}

.thumbs ul li a {
  display: block;
  padding-bottom: 60%;
  border-radius: 3px;
  border: 2px solid transparent;
}

.thumbs ul li a:hover, .thumbs ul li a:active {
  transition: .35s ease-out;
  box-shadow: 0 0 3px #fff;
  border: 2px solid #000;
  border-radius: 3px;
}

.thumbs ul li a:active {
  box-shadow: 0 0 10px #fff;
}

.features__wrap {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 47px;
  margin-bottom: 65px;
  letter-spacing: 1px;
}

@media only screen and (max-width: 840px) {
  .features__wrap {
    display: block;
    margin-bottom: 70px;
  }
}

.feature .button {
  float: right;
}

@media only screen and (max-width: 840px) {
  .feature {
    margin-bottom: 70px;
  }
  .feature:last-child {
    margin-bottom: 100px;
  }
  .feature .button {
    float: right;
  }
}

.feature__title {
  display: flex;
  margin-bottom: 31px;
}

@media only screen and (max-width: 840px) {
  .feature__title {
    margin-bottom: 10px;
  }
}

.feature__title h2 {
  height: 0;
  color: rgba(255, 255, 255, 0.8);
  font-size: 18px;
  line-height: 1.3;
  text-transform: uppercase;
  position: relative;
  font-family: bebas, sans-serif;
}

@media only screen and (max-width: 840px) {
  .feature__title h2 {
    line-height: 40px;
  }
}

@media only screen and (max-width: 620px) {
  .feature__title h2 {
    line-height: 1.3;
  }
}

.feature__icon .icon {
  flex: 0;
  display: inline;
  font-size: 46px;
  margin-right: 21px;
  line-height: 0;
  fill: rgba(255, 255, 255, 0.7);
}

.feature__text {
  font-size: 12px;
  margin-bottom: 17px;
}

@media only screen and (max-width: 840px) {
  .feature__text {
    margin-bottom: 15px;
    line-height: 1.6;
    font-size: 14px;
  }
}

.works {
  margin-bottom: 46px;
}

.work {
  display: block;
  text-decoration: none;
  word-spacing: 5px;
  letter-spacing: 1px;
}

.work:hover .work__title, .work:active .work__title {
  box-sizing: border-box;
  color: #fff;
  transition: .5s ease-out;
  letter-spacing: 3px;
}

@media only screen and (max-width: 640px) {
  .work:hover .work__title, .work:active .work__title {
    letter-spacing: 2px;
  }
}

.work:hover .work__date, .work:active .work__date {
  transition: .5s ease-out;
  letter-spacing: 2px;
}

.work__wrap {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 17px;
}

@media only screen and (max-width: 620px) {
  .work__wrap {
    grid-template-columns: repeat(2, 1fr);
  }
}

.work__image {
  overflow: hidden;
  position: relative;
  display: block;
  padding-bottom: 70%;
}

.work__image img {
  display: block;
  max-width: 100%;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.work__bottom {
  border: 1px solid #dbdbdb;
  border-top: none;
  padding: 13px;
  text-transform: uppercase;
}

.work__title {
  font-size: 16px;
  margin-bottom: 5px;
  color: #b9b9b9;
  font-family: bebas, sans-serif;
}

.work__date {
  font-size: 11px;
  line-height: 1;
  color: #E02727;
  font-family: bebas, sans-serif;
}

.footer, .footer__whitelinks {
  color: rgba(255, 255, 255, 0.59);
  overflow: hidden;
}

.footer:hover, .footer__whitelinks:hover {
  transition: 1s;
  color: #FFF;
}

.footer:hover .footer__whitelinks, .footer__whitelinks:hover .footer__whitelinks {
  transition: 1s;
  color: #FFF;
}

.footer__wrap, .footer__whitelinks__wrap {
  padding: 36px 36px 30px 36px;
  background: rgba(224, 39, 39, 0.8);
  max-width: 940px;
  position: relative;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 17px;
}

.footer__wrap:before, .footer__wrap:after, .footer__whitelinks__wrap:before, .footer__whitelinks__wrap:after {
  content: "";
  position: absolute;
  width: 100%;
  top: 0;
  height: 100%;
  background: rgba(224, 39, 39, 0.3);
}

.footer__wrap:before, .footer__whitelinks__wrap:before {
  margin-right: 10px;
  right: 100%;
}

.footer__wrap:after, .footer__whitelinks__wrap:after {
  margin-left: 10px;
  left: 100%;
}

.footer__wrap a, .footer__whitelinks__wrap a {
  text-decoration: none;
}

@media only screen and (max-width: 620px) {
  .footer__col, .footer__whitelinks__col {
    margin-bottom: 30px;
  }
  .footer__col:last-of-type, .footer__whitelinks__col:last-of-type {
    margin-bottom: 0;
  }
}

.footer__title, .footer__whitelinks__title {
  text-transform: uppercase;
  font-size: 20px;
  margin-bottom: 24px;
  font-family: bebas, sans-serif;
}

@media only screen and (max-width: 620px) {
  .footer__title, .footer__whitelinks__title {
    margin-bottom: 10px;
  }
}

.footer__text, .footer__whitelinks__text {
  font-size: 12px;
}

.footer__text p, .footer__whitelinks__text p {
  margin-bottom: 15px;
}

@media only screen and (max-width: 620px) {
  .footer__text p:nth-child(odd), .footer__whitelinks__text p:nth-child(odd) {
    display: none;
  }
}

.tweet {
  margin-bottom: 15px;
  font-size: 12px;
}

@media only screen and (max-width: 620px) {
  .tweet:nth-child(odd) {
    display: none;
  }
}

.tweet a {
  color: #f3b29d;
}

.tweet__text {
  font-size: 12px;
  margin-bottom: 5px;
}

